import React from 'react';

class Footer extends React.Component {

    bannerStyle = {
        display:'block'
    }

    render() {
        return(
            <div>
                <a style={this.bannerStyle} href="https://discord.gg/TJ3bkkY">
                    <div className={"discord-banner"}>
                        <img src="./Discord-Logo+Wordmark-White.svg" width="333"/>
                   </div>
               </a>
               <footer>
                   <div className={"copyright"}>
                        <p>yotsugidoor.moe.hm 2019 &copy;</p>
                        <p>Made by Emorard</p>
                   </div>
               </footer>
            </div>
        )
    }
}

export default Footer